button[data-start] {
  background-color: #8ed7bd;
}

button[data-stop] {
  background-color: #f1bfd0;
}

button[data-start], button[data-stop] {
  height: 22px;
  width: 70px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 2px;
  transition-property: background-color, border-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

button[data-start]:disabled, button[data-stop]:disabled {
  background-color: #c9c9d5;
}

button[data-start]:enabled:hover, button[data-start]:enabled:focus {
  background-color: #3dd09a;
  border-color: #1d8660;
}

button[data-stop]:enabled:hover, button[data-stop]:enabled:focus {
  background-color: #e195af;
  border-color: #ab5b77;
}

